import React from 'react';
import {
    CardsContainer,
    SectionHeading,
    CardsWrapper,
    CardBg,
} from './TeamCards.elements'

const TeamCards = ({ heading, data }) => {
    return (
        <>  
            <CardsContainer >
                <SectionHeading id='equipo'>{heading}</SectionHeading>
                <CardsWrapper>
                    <CardBg>
                        <iframe width="100%" height="100%" src="https://www.youtube-nocookie.com/embed/kHpj7n3_Cu4" title="YouTube video player" frameBorder="0" allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture" allowFullScreen></iframe>
                    </CardBg>
                </CardsWrapper>
            </CardsContainer>
            
        </>
    )
}

export default TeamCards;
