import React from 'react';
/* ver como omitir el picture-in-picture btn promt en opera */
import Video from '../../videos/heating-at-home.mp4';
import { getCookie, urls} from '../../utils';
import { withRouter } from 'react-router-dom';
import { toast } from 'react-toastify';
import {
    Container,
    ContinuarBg,
    VideoBg,
    FormWrap,
    Icon,
    NavIcon,
    NavIconName,
    FormContent,
    Form,
    FormH1,
    /* FormLabel, */
    FormInput,
    FormBtn,
    Text,
} from './Continuar.elements';


class Continuar extends React.Component{

    constructor(props){
        super(props)
        this.state = {
            activeItem:{
                id:null,
                email:'',
            },
        }
        this.handleChange = this.handleChange.bind(this)
        this.handleSubmit = this.handleSubmit.bind(this)
        this.cookie = this.cookie.bind(this)

        this.header = '¿Empezamos? entrenar nunca fue tan fácil'
        this.text = 'Ingresa tu email, prepararemos todo para ti'
        this.btn_content = 'Siguiente'
    };

    cookie = getCookie;

    handleChange(e){
        var name = e.target.name
        var value = e.target.value
        console.log('Name:', name, 'value:', value)
    
        this.setState({
            activeItem:{
                ...this.state.activeItem,
                email:value,
            }
        })
    }

    handleSubmit(e){
        if (this.btn_content === 'Volver'){
            this.props.history.push('');
            toast.dark("¡Gracias por tu apoyo!");
        } else {
            e.preventDefault()
            console.log('ITEM:', this.state.activeItem)
            var csrftoken = this.cookie('csrftoken')
            var slug = '/email-save/'
            var url = urls.framework  + slug

            console.log('url:', url)

            fetch(url, {
                method: 'POST',
                headers:{ 'Content-type':'application/json', 'X-CSRFToken':csrftoken, },
                body:JSON.stringify(this.state.activeItem),
            }).then((response) => {
                this.setState({
                activeItem:{
                    id:null,
                    email:'',
                }
                })
            }).catch(function(error){
                console.log('ERROR', error)
            })

            this.header = 'Aun seguimos trabajando para traerte tu mejor compañero de entrenamiento'
            this.text = '¡Ya casi estamos listos! PowerUp sigue en desarrollo'
            this.header = '¡Ya casi estamos listos! PowerUp sigue en desarrollo'
            this.text = 'Aun seguimos trabajando para traerte tu mejor compañero de entrenamiento'
            this.btn_content = 'Volver'
        }
    };

    render(){

        return (
            <>
                <Container>
                    <Icon to="/">
                            <NavIcon/><NavIconName/>
                    </Icon>
                    <ContinuarBg>
                        <VideoBg autoPlay loop muted src={Video} type='vieo/mp4'/>
                    </ContinuarBg>
                    <FormWrap>
                        
                        <FormContent>
                            <Form  onSubmit={this.handleSubmit} id='form' action="#">
                                <FormH1>{this.header}</FormH1>
                                <Text>{this.text}</Text>
                    
                                {this.btn_content !== 'Volver' ? <FormInput onChange={this.handleChange} id='email' value={this.state.activeItem.title}  name='email' type='email' required placeholder="Tu email" /> : null}
                                <FormBtn id='submit' type="submit">{this.btn_content}</FormBtn>
                            </Form>
                        </FormContent>

                    </FormWrap>
                </Container>
            </>
        )
    }
}

export default withRouter(Continuar);
