import styled from 'styled-components';

export const InfoContainer = styled.div`
    color: #ffffff;

    @media screen and (max-width: 768px) {
        padding: 50px 0;
        margin-top: 0px;
        margin-bottom: 0px;
    }

    @media screen and (max-width: 400px) {
        padding: 20px 0;
        margin-top: 20px;
    }
`

export const InfoWrapper = styled.div`
    display: grid;
    z-index: 1;
    height: 80vh;  /* VER ESTO estaba en 100*/
    width: 100%;
    margin-right: auto;
    margin-left: auto;
    padding: 0 20px;
    justify-content: center;
`

export const InfoRow = styled.div`
    display: grid;
    grid-auto-columns: minmax(auto, 1fr);
    align-items: center;
    grid-template-areas: ${({imgStart}) => (imgStart ? `'col2 col1'` : `'col1 col2'`)};

    @media screen and (max-width: 768px) {
        grid-template-areas: ${({imgStart}) => (imgStart ? `'col1' 'col2'` : `'col1 col1' 'col2 col2'`)};
    }
`

export const Column1 = styled.div`
    padding: 0 15px;
    margin-bottom: 100px;
    grid-area: col1;

    @media screen and (max-width: 400px) {
        padding: 0;
    }
`

export const Column2 = styled.div`
    padding: 0 15px;
    grid-area: col2;

    @media screen and (max-width: 400px) {
        margin: -20px;
    }
`

export const TextWrapper = styled.div`
    max-width: 700px;
    padding-top: 0;
    margin: auto;
`

export const TopLine = styled.p`
    color: #ffffff;
    font-size: 1.85rem;
    line-height: 2.5rem;
    font-weight: 400;
    letter-spacing: 1.4px;
    margin-bottom: 16px;
`

export const Message = styled.h1`
    margin-bottom: 25px;
    font-size: 1.25rem;
    line-height: 1.9rem;
    font-weight: 300;
    color: #ffffff99;

    @media screen and (max-width: 480px) {
        font-size: 1rem;
    }

`

export const ImgWrapper = styled.div`
    display: flex;
    justify-content: center;
    align-items: center;  /*  VER ESTO*/
    width: 100%;
`

export const Img = styled.img`
    padding: 0;
    border: 0;
    max-width: 100%;
    display: inline-block;
    max-height: 80vh;
    object-fit: cover;
    padding-bottom: 20px;

    @media screen and (max-width: 786px) {
        position: absolute;
        max-height: 500px;
    }
    @media screen and (max-width: 400px) {
        position: absolute;
        max-height: 350px;
    }

`