class InfoSection extends React.Component{

    constructor(props){
        super(props)
        this.state = {
            sectionList:[],
            activeItem:{
                id:null,
                topLine:'',
                message:'',
                imgStart:true,
                img:'',
                alt:'',
            },
        }

        this.fetchSections = this.fetchSections.bind(this)
    };

    cookie = getCookie;

    componentDidMount(){
        this.fetchSections()
    }
   
    fetchSections(){
        console.log('Fetching...')
        var slug = '/info-sections/'
        var url = urls.framework + slug
    
        fetch(url)
        .then(response => response.json())
        .then(data =>
          this.setState({
            sectionList:data
          })
        )
    }

    render(){
        var infoSection = this.state.sectionList
        return (
            <div id='features'>
                {infoSection.map(function(infoSection, index){
                    return(
                        <InfoContainer key={index}>
                            <InfoWrapper>
                                <InfoRow imgStart={infoSection.imgStart}>
                                    <Column1>
                                        <TextWrapper>
                                            <TopLine>{infoSection.topLine}</TopLine>
                                            <Message>{infoSection.message}</Message>
                                        </TextWrapper>
                                    </Column1>
                                    <Column2>
                                        <ImgWrapper>
                                            <Img src={urls.back + infoSection.img} alt={infoSection.alt}/>
                                        </ImgWrapper>
                                    </Column2>
                                </InfoRow>
                            </InfoWrapper>
                        </InfoContainer>                          
                    )
                })}
 
            </div>
        )
    }
}

export default InfoSection;